import React, { useContext } from "react";
import { ResourceContext } from "../../components/Page";
import cs from "./Rockets.module.css";
import { Link } from "@reach/router";

const Rockets = () => {
  const [resource] = useContext(ResourceContext);
  const rockets = resource.rockets.read();
  return (
    <section className={cs.content}>
      <h1>All SpaceY rockets</h1>
      <ul className={cs.rockets}>
        {rockets.map(rocket => (
          <li key={rocket.rocket_id}>
            <Link to={String(rocket.rocket_id)}>{rocket.rocket_name}</Link>
          </li>
        ))}
      </ul>
    </section>
  );
};

export default Rockets;
